import browserInfo from './helpers/browser_detect';

if (!Uint8Array.prototype.slice) {
  // eslint-disable-next-line no-extend-native
  Object.defineProperty(Uint8Array.prototype, 'slice', {
    value(begin, end) {
      return new Uint8Array(Array.prototype.slice.call(this, begin, end));
    },
  });
}

export const polyfillInit = async () => {
  const browser = browserInfo();

  if (browser === 'IE' || browser === 'Firefox' || browser === 'Edge') {
    if (!window['asmCrypto']) {
      window['asmCrypto'] = await import('asmcrypto.js/asmcrypto.all.es8.js');
    }

    if (!window['elliptic']) {
      window['elliptic'] = (await import('elliptic/lib/elliptic.js')).default;
    }

    await import('webcrypto-liner/build/webcrypto-liner.shim.mjs');
  }

  if (!window['protobuf']) {
    await import('protobufjs/dist/protobuf.js');
  }

  if (!window['WebcryptoSocket']) {
    await import('@webcrypto-local/client/build/webcrypto-socket.mjs');
  }
};
